import fetchRequest from './fetchRequest';
import {ENDPOINTS} from 'settings';

export const RESET_STATE = 'RESET_STATE';
export const PASSWORD_SENT = 'PASSWORD_SENT';
export const REMIND_ERROR = 'REMIND_ERROR';

function resetRemindPasswordState() {
    return {
        type: RESET_STATE
    };
}

function passwordSent() {
    return {
        type: PASSWORD_SENT
    };
}

function remindError() {
    return {
        type: REMIND_ERROR
    };
}

export function resetState() {
    return dispatch => {
        dispatch(resetRemindPasswordState());
    };
};

export function remindPassword(payload) {
    const {email} = payload;

    return dispatch => {
        const endpoint = ENDPOINTS.REMIND_PASSWORD;
        const method = 'POST';
        const data = {email};

        dispatch(resetRemindPasswordState());
        fetchRequest(dispatch, {endpoint, method, data})
            .then(() => {
                dispatch(passwordSent());
            })
            .catch(error => {
                dispatch(remindError());
                console.error(error);
            });
    };
}



