import React, {PropTypes} from 'react';
import {bindActionCreators} from 'redux';

import * as userActions from 'actions/user';
import * as participantsActions from 'actions/participants';

import Layout from 'components/global/Layout';
import Sidebar from 'components/global/Sidebar';

import Headline from 'components/admin/Headline';
import Profile from 'components/admin/Profile';
import Participant from 'components/participants/Participant';
import ParticipantsTable from 'components/participants/ParticipantsTable';
import ParticipantForm from 'components/participants/ParticipantForm';

import Login from 'components/user/Login';
import RemindPassword from 'components/user/RemindPassword';

const IS_ADMIN = true;

class AdminLayout extends Layout {

    componentDidUpdate(prevProps) {
        const {user} = this.props;
        if (user.id && !prevProps.user.id) {
            this._redirectToAdmin();
        }
    }

    getUser(payload) {
        const {dispatch} = this.props;
        dispatch(userActions.getUser(
            Object.assign({}, payload, {unauthorizedError: IS_ADMIN})
        ));
    }

    _redirectToAdmin() {
        const {history} = this.props;
        history.replaceState(null, 'admin');
    }

    _getContent() {
        const {
            dispatch,
            history,
            user,
            participants,
            participant,
            location: {pathname},
            params
        } = this.props;
        const {category, id, action} = params;

        if (!user.id && !user.accessToken) {
            switch (category) {
                case 'remind-password':
                    return <RemindPassword history={history} />;

                case 'sign-in':
                default:
                    return <Login history={history} />;
            }
        }

        if (user.id) {
            switch (category) {
                case 'profile':
                    const bindUserActions = bindActionCreators(userActions, dispatch);
                    return <Profile user={user} {...bindUserActions} />;

                case 'participants':
                default:
                    const bindParticipantActions = bindActionCreators(participantsActions, dispatch);

                    if (id) {
                        const actionCase = id === 'new' ? 'edit' : action;

                        switch (actionCase) {
                            case 'edit':
                                return <ParticipantForm history={history} params={params} participant={participant} {...bindParticipantActions} />;

                            default:
                                return <Participant id={id} user={user} {...bindParticipantActions} />;
                        }
                    } else {
                        return <ParticipantsTable isAdmin={IS_ADMIN} pathname={pathname} user={user} participants={participants} {...bindParticipantActions} />;
                    }
            }
        }
    }

    render() {
        const {params, user} = this.props;

        return (
            <div className="container-fluid">
                <div className="page-header">
                    <Headline signOut={this._signOut} user={user} />
                    <h1>
                        Admin <small>- {this._getPageTitle()}</small>
                    </h1>
                </div>
                <div className="row">
                    <div className="col-sm-3">
                        <Sidebar isAdmin={IS_ADMIN} params={params} user={user} />
                    </div>
                    <div className="col-sm-9">
                        {this._getContent()}
                    </div>
                </div>
            </div>
        );

    }
}

AdminLayout.propTypes = {
    dispatch: PropTypes.func.isRequired,
    history: PropTypes.object.isRequired,
    location: PropTypes.object.isRequired,
    params: PropTypes.object.isRequired,
    user: PropTypes.object.isRequired,
    participants: PropTypes.array.isRequired,
    participant: PropTypes.object.isRequired
};

export default AdminLayout;



