import {RESET_STATE, LOGIN_REQUEST, LOGIN_FAILED, USER_UNAUTHORIZED} from 'actions/user';

const defaultState = {
    error: null
};

export default function userLoginReducer(state = defaultState, action) {
    switch (action.type) {
        case RESET_STATE:
            return Object.assign({}, state, defaultState);

        case LOGIN_REQUEST:
            return Object.assign({}, state, {error: null});

        case LOGIN_FAILED:
            return Object.assign({}, state, {
                error: 'Login failed. Please check your email and password'
            });

        case USER_UNAUTHORIZED:
            return Object.assign({}, state, {
                error: 'You have to be authenticated before continuing to Admin'
            });

        default:
            return state;
    }
};



