import React, {Component, PropTypes} from 'react';
import {connect, Provider} from 'react-redux';
import {Router} from 'react-router';

import configureStore from './configureStore';
import Public from 'components/global/Layout';
import Admin from 'components/admin/Layout';

const store = configureStore();

function mapStateToProps(state) {
    const {user, participants, participant} = state;
    return {user, participants, participant};
}

const configRoutes = [
    {
        path: 'admin(/:category)(/:id)(/:action)',
        component: connect(mapStateToProps)(Admin)
    },
    {
        path: '/(:category)(/:id)(/:action)',
        component: connect(mapStateToProps)(Public)
    }
];

class Root extends Component {
    render() {
        return (
            <Provider store={store}>
                <Router history={this.props.history} routes={configRoutes} />
            </Provider>
        );
    }
}

Root.propTypes = {
    history: PropTypes.object.isRequired
};

export default Root;



