import React, {Component, PropTypes} from 'react';
import {Link} from 'react-router';
import style from '../../styles/global.scss';

class RemindPasswordForm extends Component {

    constructor(props) {
        super(props);

        this._changePassword = this._changePassword.bind(this);
        this._onChange = this._onChange.bind(this);

        this.state = {
            pin: '',
            password1: '',
            password2: ''
        };
    }

    _checkPassword(password) {
        const re = /(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{6,}/;
        return re.test(password);
    }

    _changePassword(event) {
        event.preventDefault();
        const {pin, password1, password2} = this.state;
        const {changePassword} = this.props;
        let error = null;

        this.setState({error});

        if (!pin || pin.length !== 6) {
            error = 'Please enter a valid PIN';
        } else if (password1 !== password2) {
            error = 'Passwords should match';
        } else if (!this._checkPassword(password1)) {
            error = (
                <div>
                    Please verify that your password matches these criterias:
                    <ul>
                        <li>matches a string of six or more characters;</li>
                        <li>that contains at least one digit;</li>
                        <li>at least one lowercase character; and</li>
                        <li>at least one uppercase character:</li>
                    </ul>
                </div>
            );
        }

        if (error) {
            this.setState({error});
        } else {
            changePassword({
                pin,
                password: password1
            });
        }
    }

    _errorMessage() {
        const {error} = this.state;

        if (error) {
            return (
                <div className="alert alert-danger">{error}</div>
            );
        }
    }

    _onChange(event) {
        const {target: {name, value}} = event;
        this.setState({
            [name]: value
        });
    }

    render() {
        const {pin, password1, password2} = this.state;
        const {email} = this.props;

        return (
            <form className="form-horizontal" onSubmit={this._changePassword}>
                {this._errorMessage()}
                <div className="form-group">
                    <label htmlFor="inputPin" className="col-sm-3 control-label">PIN</label>
                    <div className="col-sm-9">
                        <input name="pin" value={pin} onChange={this._onChange} type="text" className="form-control" id="inputPin" placeholder="PIN" />
                    </div>
                </div>

                <div className="form-group">
                    <label htmlFor="inputPassword1" className="col-sm-3 control-label">New Password</label>
                    <div className="col-sm-9">
                        <input name="email" value={email} type="text" className={style.hidden} />
                        <input name="password1" value={password1} onChange={this._onChange} type="password" className="form-control" id="inputPassword1" />
                    </div>
                </div>

                <div className="form-group">
                    <label htmlFor="inputPassword2" className="col-sm-3 control-label">Repeat Password</label>
                    <div className="col-sm-9">
                        <input name="password2" value={password2} onChange={this._onChange} type="password" className="form-control" id="inputPassword2" />
                    </div>
                </div>

                <div className="form-group">
                    <div className="col-sm-offset-3 col-sm-9">
                        <button type="submit" className="btn btn-default">Change Password</button>
                        <Link to="/admin/sign-in" className="btn btn-link">Back to login</Link>
                    </div>
                </div>
            </form>
        );
    }
}

RemindPasswordForm.propTypes = {
    changePassword: PropTypes.func.isRequired,
    email: PropTypes.string.isRequired
};

export default RemindPasswordForm;



