import React, {Component, PropTypes} from 'react';
import ReactFileInput from 'react-file-input';
import classnames from 'classnames';

import style from '../../styles/global.scss';

const MAX_FILE_SIZE = 500; // Size in KB

class Photo extends Component {
    constructor(props) {
        super(props);

        this._onChange = this._onChange.bind(this);
        this._toggleChangePhoto = this._toggleChangePhoto.bind(this);

        this.state = {
            change: false,
            error: false
        };
    }

    componentWillReceiveProps() {
        this.setState({error: false});
    }

    _onChange(event) {
        const {name, onChange} = this.props;
        const file = event.target.files[0];

        if (!file) {
            return;
        }

        const fileSize = file.size / 1000;

        if (fileSize > MAX_FILE_SIZE) {
            this.setState({error: true});
            return;
        }

        onChange({name, value: null});
        const reader = new FileReader();

        reader.onload = output => {
            const {result: value} = output.target;
            onChange({name, value});
        };

        reader.readAsDataURL(file);
    }

    _getError() {
        const {error} = this.state;

        if (error) {
            return (
                <p className="help-block">
                    Photo should be less than {MAX_FILE_SIZE} Kb size.
                </p>
            );
        }
    }

    _toggleChangePhoto(event) {
        event.preventDefault();
        event.target.blur();
        const {change} = this.state;
        this.setState({change: !change});
    }

    _renderPhoto() {
        const {value} = this.props;
        return (
            <div className="form-group">
                <div className="col-sm-10 col-sm-offset-2">
                    <img className={`${style.profileImage} img-thumbnail`} src={value} />
                </div>
            </div>
        );
    }

    _renderPhotoField() {
        const {error} = this.state;
        const className = classnames('form-group', {
            'has-error': error
        });

        return (
            <div className={className}>
                <label className="col-sm-2 control-label" forHtml="exampleInputFile">Photo</label>
                <div className="col-sm-10">
                    <ReactFileInput name="myImage"
                        accept=".png,.jpg"
                        value="dsadas"
                        placeholder="Photo"
                        className="form-control"
                        onChange={this._onChange} />
                    {this._getError()}
                </div>
            </div>
        );
    }

    render() {
        return (
            <div>
                {this._renderPhoto()}
                {this._renderPhotoField()}
            </div>
        );
    }
}

Photo.propTypes = {
    onChange: PropTypes.func.isRequired,
    name: PropTypes.string.isRequired,
    fieldId: PropTypes.string.isRequired,
    label: PropTypes.node.isRequired,
    value: PropTypes.string,
    fieldProps: PropTypes.object,
    placeholder: PropTypes.string
};

export default Photo;



