import Photo from '../fieldTypes/Photo';
import ReadOnly from '../fieldTypes/ReadOnly';
import Text from '../fieldTypes/Text';
import Select from '../fieldTypes/Select';

import {countries} from 'country-data';

const userProfileFields = [
    {
        name: 'photo',
        label: 'Photo',
        component: Photo
    },
    {
        name: 'email',
        label: 'Email',
        component: ReadOnly
    },
    {
        name: 'firstName',
        label: 'First Name',
        component: Text
    },
    {
        name: 'lastName',
        label: 'Last Name',
        component: Text
    },
    {
        name: 'country',
        label: 'Country',
        component: Select,
        fieldProps: {
            options: countries.all,
            labelKey: 'name',
            valueKey: 'alpha2',
            disabled: true
        }
    },
    {
        name: 'address',
        label: 'Address',
        component: Text
    },
    {
        name: 'position',
        label: 'Position',
        component: Text
    },
    {
        name: 'phoneNumber',
        label: 'Phone Number',
        component: Text
    },
    {
        name: 'dojo',
        label: 'Dojo',
        component: Text
    }
];

export default userProfileFields;



