import {getAccessToken, setAccessToken, removeAccessToken} from 'actions/accessToken';
import {USER_LOADED, USER_UPDATED, USER_LOGOUT} from 'actions/user';

const defaultUserState = {
    accessToken: getAccessToken(),
    id: null,
    photo: '',
    email: '',
    firstName: '',
    lastName: '',
    country: '',
    address: '',
    position: '',
    phoneNumber: '',
    dojo: ''
};

export default function userReducer(state = defaultUserState, action) {
    switch (action.type) {
        case USER_LOADED:
        case USER_UPDATED:
            const {user} = action;
            setAccessToken(`${user.id}_${user.accessToken}`);
            return Object.assign({}, state, user);

        case USER_LOGOUT:
            removeAccessToken();
            return Object.assign({}, state, defaultUserState, {
                accessToken: null,
                id: null
            });

        default:
            return state;
    }
};



