import React, {Component, PropTypes} from 'react';
import {Link} from 'react-router';

class RemindPasswordForm extends Component {

    constructor(props) {
        super(props);

        this._remindPassword = this._remindPassword.bind(this);
        this._onChange = this._onChange.bind(this);

        this.state = {
            email: ''
        };
    }

    componentWillReceiveProps() {
        this.setState({email: ''});
    }

    _remindPassword(event) {
        event.preventDefault();
        const {remindPassword} = this.props;
        const {email} = this.state;

        remindPassword({email});
    }

    _onChange(event) {
        let {target: {value: email}} = event;
        email = email.trim();
        this.setState({email});
    }

    render() {
        const {email} = this.state;

        return (
            <form className="form-horizontal" onSubmit={this._remindPassword}>
                <div className="form-group">
                    <label htmlFor="inputEmailAddress" className="col-sm-3 control-label">
                        Email address
                    </label>
                    <div className="col-sm-9">
                        <input value={email}
                            onChange={this._onChange}
                            type="text"
                            className="form-control"
                            id="inputEmailAddress"
                            placeholder="your-email@address.com" />
                    </div>
                </div>

                <div className="form-group">
                    <div className="col-sm-offset-3 col-sm-9">
                        <button type="submit" className="btn btn-default">Remind password</button>
                        <Link to="/admin/sign-in" className="btn btn-link">Back to login</Link>
                    </div>
                </div>
            </form>
        );
    }
}

RemindPasswordForm.propTypes = {
    remindPassword: PropTypes.func.isRequired
};

export default RemindPasswordForm;



