import React, {Component, PropTypes} from 'react';
import {Link} from 'react-router';
import {publicNavigation, adminNavigation} from './sidebarNavigation';

import style from '../../styles/global.scss';

class Sidebar extends Component {

    _getPublicNav() {
        const {isAdmin, params: {category}} = this.props;

        return (
            <ul className="nav nav-pills nav-stacked">
                {publicNavigation.map((nav, index) => {
                    const isCategory = !isAdmin && category && category === nav.key;
                    const noCatFirst = !isAdmin && !category && index === 0;
                    const isSelected = isCategory || noCatFirst ? 'active' : '';

                    if (nav.hidden) {
                        return null;
                    } else {
                        return (
                            <li key={nav.key} className={isSelected}>
                                <Link to={nav.key}>{nav.name}</Link>
                            </li>
                        );
                    }
                })}
            </ul>
        );
    }

    _getAdminNav() {
        const {isAdmin, params: {category}, user} = this.props;

        if (user.id) {
            return (
                <ul className="nav nav-pills nav-stacked">
                    {adminNavigation.map((nav, index) => {
                        const isCategory = isAdmin && category && category === nav.key;
                        const noCatFirst = isAdmin && !category && index === 0;
                        const isSelected = isCategory || noCatFirst ? 'active' : '';

                        if (nav.hidden) {
                            return null;
                        } else {
                            return (
                                <li key={nav.key} className={isSelected}>
                                    <Link to={`admin/${nav.key}`}>{nav.name}</Link>
                                </li>
                            );
                        }
                    })}
                </ul>
            );
        } else {
            return (
                <ul className="nav nav-pills nav-stacked">
                    <li><Link to='admin/sign-in'>Sign In</Link></li>
                </ul>
            );
        }
    }

    render() {
        return (
            <div>
                {this._getPublicNav()}
                <h4>Registration</h4>
                {this._getAdminNav()}
                <div className={style.poweredBy}>
                    Powered by <a href="mailto:kumite.technology@gmail.com">KumiteTechnology</a>
                </div>
            </div>
        );
    }
}

Sidebar.propTypes = {
    params: PropTypes.object.isRequired,
    user: PropTypes.object.isRequired,
    isAdmin: PropTypes.bool
};

export default Sidebar;



