export function resetParticipants() {
    return dispatch => {
        dispatch({type: PARTICIPANTS_RESET});
    };
}

export function getParticipants(payload = {}) {
    const {userId} = payload;

    return dispatch => {
        const endpoint = tokenReplace(ENDPOINTS.PARTICIPANTS);
        const accessToken = getAccessToken();

        dispatch({type: PARTICIPANTS_REQEUST});

        fetchRequest(dispatch, {endpoint, accessToken, data: {userId}})
            .then(response => {
                dispatch({
                    type: PARTICIPANTS_LOADED,
                    participants: response.participants
                });
            })
            .catch(error => {
                // if (userId) {
                //     dispatch({
                //         type: PARTICIPANTS_LOADED,
                //         participants: PARTICIPANTS
                //     });
                // }
                console.error(error);
            });
    };
}

export function resetParticipant() {
    return dispatch => {
        dispatch({type: PARTICIPANT_RESET});
    };
}

export function getParticipant(payload = {}) {
    const {id} = payload;
    const accessToken = getAccessToken();

    return dispatch => {
        const endpoint = tokenReplace(ENDPOINTS.PARTICIPANT, {id});

        dispatch({type: PARTICIPANT_REQUEST});

        fetchRequest(dispatch, {endpoint, accessToken})
            .then(response => {
                dispatch({
                    type: PARTICIPANT_LOADED,
                    participant: response.participant
                });
            })
            .catch(error => {
                // dispatch({
                //     type: PARTICIPANT_LOADED,
                //     participant: PARTICIPANTS.find(p => {
                //         return p.id === Number.parseInt(id);
                //     })
                // });
                console.error(error);
            });
    };
}

export function saveParticipant(participant = {}) {
    const accessToken = getAccessToken();

    console.log(participant);

    return dispatch => {
        const {id} = participant;
        const isNew = !id;
        const endpoint = tokenReplace(ENDPOINTS.PARTICIPANT, {id});
        const method = isNew ? 'POST' : 'PUT';

        dispatch({type: PARTICIPANT_REQUEST});

        fetchRequest(dispatch, {endpoint, method, accessToken})
            .then(response => {
                dispatch({
                    type: isNew ? PARTICIPANT_CREATED : PARTICIPANT_LOADED,
                    participant: response.participant
                });
            })
            .catch(error => {
                // dispatch({
                //     type: isNew ? PARTICIPANT_CREATED : PARTICIPANT_LOADED,
                //     participant: isNew ? Object.assign(participant, {id: 50}) : participant
                // });
                console.error(error);
            });
    };
}



