import React from 'react';
import {render} from 'react-dom';
import Root from 'Root';
import {createHashHistory} from 'history';
const history = createHashHistory();
const head = document.head;

const injectStyle = (href) => {
    return new Promise(resolve => {
        const link = document.createElement('link');

        Object.assign(link, {
            type: 'text/css',
            rel: 'stylesheet',
            onload: () => resolve(),
            href
        });

        head.appendChild(link);
    });
};

const styles = [
    // 'https://maxcdn.bootstrapcdn.com/bootstrap/3.3.6/css/bootstrap.min.css',
    // 'https://bootswatch.com/sandstone/bootstrap.min.css',
    // 'https://bootswatch.com/paper/bootstrap.min.css',
    // 'https://bootswatch.com/slate/bootstrap.min.css',
    'https://bootswatch.com/united/bootstrap.min.css'
];

const promises = [];

styles.forEach(style => {
    const promise = injectStyle(style);
    promises.push(promise);
});

Promise.all(promises).then(() => {
    render(<Root history={history} />, document.getElementById('kyokushin-registration'));
});



