import React, {Component, PropTypes} from 'react';
import ReactSelect from 'react-select';
import classnames from 'classnames';

import 'react-select/scss/default.scss';
import style from '../../styles/select.scss';

class Select extends Component {
    constructor(props) {
        super(props);

        this._onChange = this._onChange.bind(this);
    }

    _onChange(value) {
        const {name, onChange} = this.props;
        onChange({name, value});
    }

    render() {
        const {fieldId, label, value, fieldProps} = this.props;
        const className = classnames({
            [style.disabled]: fieldProps && fieldProps.disabled
        });

        return (
            <div className="form-group">
                <label forHtml={fieldId} className="col-sm-2 control-label">
                    {label}
                </label>
                <div className="col-sm-10">
                    <ReactSelect clearable={false}
                        className={className}
                        value={value}
                        onChange={this._onChange}
                        {...fieldProps} />
                </div>
            </div>
        );
    }
}

Select.propTypes = {
    onChange: PropTypes.func.isRequired,
    name: PropTypes.string.isRequired,
    fieldId: PropTypes.string.isRequired,
    label: PropTypes.node.isRequired,
    value: PropTypes.string,
    fieldProps: PropTypes.object,
    placeholder: PropTypes.string
};

export default Select;



