import React, {Component} from 'react';
import classnames from 'classnames';

import style from '../../styles/global.scss';

class Profile extends Component {

    constructor(props) {
        super(props);

        this._onChange = this._onChange.bind(this);
        this._onSubmit = this._onSubmit.bind(this);

        this.state = {
            loading: false,
            [this.stateNamespace]: props[this.stateNamespace]
        };
    }

    componentWillReceiveProps(nextProps) {
        this.setState({
            loading: false,
            [this.stateNamespace]: nextProps[this.stateNamespace]
        });
    }

    edit() {}

    save() {}

    _onChange(payload) {
        const {name, value} = payload;
        const state = this.state[this.stateNamespace];

        this.setState({
            [this.stateNamespace]: Object.assign({}, state, {
                [name]: value
            })
        });
    }

    _onSubmit(event) {
        event.preventDefault();
        this.setState({loading: true});

        const state = this.state[this.stateNamespace];
        if (state.id) {
            this.edit();
        } else {
            this.save();
        }
    }


    render() {
        const {loading} = this.state;
        const state = this.state[this.stateNamespace];
        const className = classnames('form-horizontal', {
            [style.loading]: loading
        });

        return (
            <form className={className} onSubmit={this._onSubmit}>
                {this.formFields.map(field => {
                    const {name, label, fieldProps} = field;
                    const fieldId = `input-${name}`;
                    const value = state[name];

                    return (
                        <field.component key={field.name}
                            name={name}
                            fieldId={fieldId}
                            label={label}
                            value={value}
                            onChange={this._onChange}
                            fieldProps={fieldProps} />
                    );
                })}

                <div className="form-group">
                    <div className="col-sm-offset-2 col-sm-10">
                        <button type="submit" className="btn btn-default">Save</button>
                    </div>
                </div>
            </form>
        );
    }
}

Profile.prototype.stateNamespace = 'user';
Profile.prototype.formFields = [];

export default Profile;



