import React, {Component, PropTypes} from 'react';
import {Link} from 'react-router';
import {connect} from 'react-redux';

import {login} from 'actions/user';

class Login extends Component {

    constructor(props) {
        super(props);

        this._onChange = this._onChange.bind(this);
        this._logIn = this._logIn.bind(this);

        this.state = {
            email: '',
            password: ''
        };
    }

    componentWillReceiveProps(nextProps) {
        const {user} = this.props;
        if (!user.id && nextProps.user.id) {
            this._redirectToAdmin();
        }
    }

    _onChange(event) {
        const {target: {name, value}} = event;
        this.setState({
            [name]: value.trim()
        });
    }

    _redirectToAdmin() {
        const {history} = this.props;
        history.replaceState(null, 'admin');
    }

    _logIn(event) {
        event.preventDefault();
        const {dispatch} = this.props;
        const {email, password} = this.state;
        dispatch(login({email, password}));
    }

    _errorMessage() {
        const {userLogin} = this.props;

        if (userLogin.error) {
            return (
                <div className="alert alert-danger">{userLogin.error}</div>
            );
        }
    }

    render() {
        return (
            <div className="jumbotron">
                {this._errorMessage()}
                <form className="form-horizontal" onSubmit={this._logIn}>
                    <div className="form-group">
                        <label htmlFor="inputEmailAddress" className="col-sm-3 control-label">
                            Email address
                        </label>
                        <div className="col-sm-9">
                            <input name="email"
                                onChange={this._onChange}
                                type="text"
                                className="form-control"
                                id="inputEmailAddress"
                                placeholder="your-email@address.com" />
                        </div>
                    </div>
                    <div className="form-group">
                        <label htmlFor="inputPassword3" className="col-sm-3 control-label">
                            Password
                        </label>
                        <div className="col-sm-9">
                            <input name="password"
                                onChange={this._onChange}
                                type="password"
                                className="form-control"
                                id="inputPassword3"
                                placeholder="Password" />
                        </div>
                    </div>
                    <div className="form-group">
                        <div className="col-sm-offset-3 col-sm-9">
                            <button type="submit" className="btn btn-default">Log in</button>
                            <Link to="/admin/remind-password" className="btn btn-link">Forgot password?</Link>
                        </div>
                    </div>
                </form>
            </div>
        );
    }
}

Login.propTypes = {
    dispatch: PropTypes.func.isRequired,
    history: PropTypes.object.isRequired,
    user: PropTypes.object.isRequired,
    userLogin: PropTypes.object.isRequired
};

function mapStateToProps(state) {
    const {user, userLogin} = state;
    return {
        user,
        userLogin
    };
}

export default connect(mapStateToProps)(Login);



