import {
    PARTICIPANTS_REQEUST,
    PARTICIPANTS_RESET,
    PARTICIPANTS_LOADED,
    PARTICIPANT_REQEUST,
    PARTICIPANT_RESET,
    PARTICIPANT_LOADED,
    PARTICIPANT_CREATED
} from '../actions/participants';

const defaultParticipantsState = [];
const defaultParticipantState = {
    id: null,
    firstName: '',
    lastName: '',
    birthDay: '',
    country: '',
    dojo: '',
    weight: '',
    gender: '',
    category: '',
    photo: ''
};

export function participantsReducer(state = defaultParticipantsState, action) {
    switch (action.type) {
        case PARTICIPANTS_REQEUST:
        case PARTICIPANTS_RESET:
            return defaultParticipantsState;

        case PARTICIPANTS_LOADED:
            const {participants} = action;
            return participants;

        default:
            return state;
    }
};

export function participantReducer(state = defaultParticipantState, action) {
    switch (action.type) {
        case PARTICIPANT_REQEUST:
        case PARTICIPANT_RESET:
            return Object.assign({}, state, defaultParticipantState);

        case PARTICIPANT_LOADED:
        case PARTICIPANT_CREATED:
            const {participant} = action;
            return Object.assign({}, state, participant);

        default:
            return state;
    }
};



