import {getUserId, getAccessToken} from './accessToken';
import fetchRequest from './fetchRequest';
import tokenReplace from './tokenReplace';
import {ENDPOINTS} from 'settings';

export const USER_LOGOUT = 'USER_LOGOUT';
export const USER_LOADED = 'USER_LOADED';
export const USER_LOADING = 'USER_LOADING';
export const USER_UPDATED = 'USER_UPDATED';

export const RESET_STATE = 'RESET_STATE';
export const LOGIN_REQUEST = 'LOGIN_REQUEST';
export const LOGIN_FAILED = 'LOGIN_FAILED';
export const USER_UNAUTHORIZED = 'USER_UNAUTHORIZED';

function userHasLoaded(user) {
    return {
        type: USER_LOADED,
        user
    };
}

export function userHasLoggedOut() {
    return {
        type: USER_LOGOUT
    };
}

export function resetState() {
    return dispatch => {
        dispatch({type: RESET_STATE});
    };
}

export function getUser() {
    const accessToken = getAccessToken();
    const id = getUserId();

    return dispatch => {
        const endpoint = tokenReplace(ENDPOINTS.USER, {id});

        fetchRequest(dispatch, {endpoint, accessToken})
            .then(response => {
                dispatch(userHasLoaded(response.user));
            })
            .catch(error => {
                console.error(error);
            });
    };
}

export function editUser(payload = {}) {
    const {id} = payload;
    const accessToken = getAccessToken();

    return dispatch => {
        const endpoint = tokenReplace(ENDPOINTS.USER, {id});
        const method = 'PUT';
        const data = payload;

        dispatch({type: USER_LOADING});

        fetchRequest(dispatch, {endpoint, method, accessToken, data})
            .then(response => {
                dispatch({
                    type: USER_UPDATED,
                    user: response.user
                });
            })
            .catch(error => {
                console.error(error);
            });
    };
}

export function changePassword(payload = {}) {
    const {pin, email, password} = payload;

    return dispatch => {
        const endpoint = ENDPOINTS.CHANGE_PASSWORD;
        const method = 'POST';
        const data = {
            pin,
            email,
            password
        };

        fetchRequest(dispatch, {endpoint, method, data})
            .then(response => {
                dispatch(userHasLoaded(response.user));
            })
            .catch(error => {
                console.error(error);
            });
    };
}

export function login(payload = {}) {
    const {email, password} = payload;

    return dispatch => {
        const endpoint = ENDPOINTS.LOGIN;
        const method = 'POST';
        const data = {email, password};

        dispatch({type: LOGIN_REQUEST});
        fetchRequest(dispatch, {endpoint, method, data})
            .then(response => {
                dispatch(userHasLoaded(response.user));
            })
            .catch(error => {
                dispatch({type: LOGIN_FAILED});
                console.error(error);
            });
    };
}

export function signOut() {
    const accessToken = getAccessToken();

    return dispatch => {
        const endpoint = ENDPOINTS.SIGN_OUT;

        dispatch(userHasLoggedOut());
        fetchRequest(dispatch, {endpoint, accessToken})
            .then(() => {
                dispatch(userHasLoggedOut());
            })
            .catch(error => {
                console.error(error);
            });
    };
}



