import React, {Component, PropTypes} from 'react';
import {Link} from 'react-router';
import ParticipantRow from './ParticipantRow';

class ParticipantsTable extends Component {

    componentDidMount() {
        const {isAdmin, user, getParticipants} = this.props;
        let payload;

        if (isAdmin) {
            payload = {userId: user.id};
        }

        getParticipants(payload);
    }

    componentWillUnmount() {
        const {resetParticipants} = this.props;
        resetParticipants();
    }

    _getFooter() {
        const {isAdmin} = this.props;

        if (isAdmin) {
            return (
                <tfoot>
                    <tr>
                        <td colSpan="7">
                            <Link to={`admin/participants/new`} className="btn btn-default">
                                Add New
                            </Link>
                        </td>
                    </tr>
                </tfoot>
            );
        }
    }

    render() {
        const {isAdmin, pathname, participants} = this.props;

        return (
            <table className="table table-hover">
                <thead>
                    <tr>
                        <th>#</th>
                        <th>Name</th>
                        <th>Country</th>
                        <th>Weight</th>
                        <th>Category</th>
                        <th>Date of Birth</th>
                        {(() => {
                            if (isAdmin) {
                                return <th />;
                            }
                        })()}
                    </tr>
                </thead>
                <tbody>
                    {participants.map((participant, index) => {
                        return (
                            <ParticipantRow key={participant.id}
                                index={index + 1}
                                isAdmin={isAdmin}
                                pathname={pathname}
                                {...participant} />
                        );
                    })}
                </tbody>
                {this._getFooter()}
            </table>
        );
    }
}

ParticipantsTable.propTypes = {
    isAdmin: PropTypes.bool,
    pathname: PropTypes.string.isRequired,
    user: PropTypes.object.isRequired,
    participants: PropTypes.array.isRequired,
    resetParticipants: PropTypes.func.isRequired,
    getParticipants: PropTypes.func.isRequired
};

export default ParticipantsTable;



