import fetch from 'isomorphic-fetch';
import qs from 'qs';
import {XHR_HEADERS} from '../settings';
import {userHasLoggedOut} from './user';

export default function fetchRequest(dispatch, payload = {}) {
    const {endpoint, accessToken, data} = payload;
    const method = payload.method || 'GET';
    const fetchData = {
        headers: XHR_HEADERS,
        // credentials: 'include',
        method
    };
    const query = {accessToken};

    switch (method) {
        case 'POST':
        case 'PUT':
            Object.assign(fetchData, {
                body: qs.stringify(data)
            });
            break;

        case 'GET':
            Object.assign(query, data);
            break;
    }

    return new Promise((resolve, reject) => {
        fetch(endpoint + '?' + qs.stringify(query), fetchData)
            .then(response => {
                if (response.status === 401) {
                    dispatch(userHasLoggedOut());
                    return Promise.reject('This request requires user authentication');
                } else if (response.status >= 400) {
                    return Promise.reject(response.statusText);
                }

                return Promise.resolve(response.json());
            })
            .then(response => {
                resolve(response.data);
            })
            .catch(error => {
                reject(error);
            });
    });
}



