export default function tokenReplace(str, o = {}, delim) {
    let pattern;
    delim = undefined === delim ? '{}' : delim;
    pattern = new RegExp('\\' + delim.slice(0, 1) + '+([^' + '\\' + delim.split('').join('\\') + ']*)' + '\\' + delim.slice(-1) + '+', 'g');

    if (typeof str !== 'string') {
        return str;
    }

    return str.replace(pattern, (a, b) => {
        const r = o[b];
        return typeof r === 'string' || typeof r === 'number' ? r : '';
    });
};



