import React, {Component, PropTypes} from 'react';
import {connect} from 'react-redux';

import {resetState, remindPassword as remindPasswordAction} from 'actions/remindPassword';
import {changePassword as changePasswordAction} from 'actions/user';

import RemindPasswordForm from './RemindPasswordForm';
import ChangePasswordForm from './ChangePasswordForm';

class RemindPassword extends Component {

    constructor(props) {
        super(props);

        this._remindPassword = this._remindPassword.bind(this);
        this._changePassword = this._changePassword.bind(this);

        this.state = {
            email: null
        };
    }

    componentDidMount() {
        const {dispatch} = this.props;
        dispatch(resetState());
    }

    _remindPassword(payload) {
        const {email} = payload;
        const {dispatch} = this.props;
        this.setState({email});
        dispatch(remindPasswordAction({email}));
    }

    _changePassword(payload) {
        const {email} = this.state;
        const {dispatch} = this.props;
        const data = Object.assign({}, payload, {email});
        dispatch(changePasswordAction(data));
    }

    _form() {
        const {email} = this.state;
        const {remindPassword} = this.props;

        if (remindPassword.isSent) {
            return <ChangePasswordForm email={email} changePassword={this._changePassword} />;
        } else {
            return <RemindPasswordForm remindPassword={this._remindPassword} />;
        }
    }

    _errorMessage() {
        const {remindPassword} = this.props;

        if (remindPassword.error) {
            return (
                <div className="alert alert-danger">{remindPassword.error}</div>
            );
        }
    }

    _successMessage() {
        const {remindPassword} = this.props;

        if (remindPassword.success) {
            return (
                <div className="alert alert-success">{remindPassword.success}</div>
            );
        }
    }

    render() {
        return (
            <div className="jumbotron">
                {this._errorMessage()}
                {this._successMessage()}
                {this._form()}
            </div>
        );
    }
}

RemindPassword.propTypes = {
    children: PropTypes.node,
    dispatch: PropTypes.func.isRequired,
    user: PropTypes.object.isRequired,
    remindPassword: PropTypes.object.isRequired
};

function mapStateToProps(state) {
    const {user, remindPassword} = state;
    return {user, remindPassword};
}

export default connect(mapStateToProps)(RemindPassword);



