import React, {Component, PropTypes} from 'react';
import {bindActionCreators} from 'redux';
import find from 'lodash.find';

import {getUser, signOut} from 'actions/user';
import * as participantActions from 'actions/participants';
import {publicNavigation, adminNavigation} from './sidebarNavigation';

import Headline from 'components/admin/Headline';
import Sidebar from 'components/global/Sidebar';
import ParticipantsTable from 'components/participants/ParticipantsTable';
import Participant from 'components/participants/Participant';

class Layout extends Component {

    constructor(props) {
        super(props);
        const {user} = props;

        this.bindFunctions();

        this.state = {
            userLoaded: user.accessToken && user.id
        };
    }

    componentDidMount() {
        const {userLoaded} = this.state;
        const {accessToken} = this.props.user;

        if (accessToken && !userLoaded) {
            this.getUser();
        }
    }

    componentWillReceiveProps(nextProps) {
        const {accessToken, id} = nextProps;
        if (accessToken && id) {
            this.setState({userLoaded: true});
        } else {
            this.setState({userLoaded: false});
        }
    }

    _signOut() {
        const {dispatch} = this.props;
        dispatch(signOut());
    }

    _getPageTitle() {
        let {params: {category}} = this.props;
        category = category || publicNavigation[0].key;

        const pageTitle = find(publicNavigation.concat(adminNavigation), nav => {
            return nav.key === category;
        });

        return pageTitle && pageTitle.name;
    }

    bindFunctions() {
        this._signOut = this._signOut.bind(this);
    }

    getUser(payload) {
        const {dispatch} = this.props;
        dispatch(getUser(payload));
    }

    _getContent() {
        const {
            dispatch,
            user,
            participants,
            params: {category, id}
        } = this.props;

        switch (category) {
            case 'categories':
                return null;

            case 'participants':
            default:
                const bindParticipantActions = bindActionCreators(participantActions, dispatch);

                if (id) {
                    return <Participant id={id} user={user} {...bindParticipantActions} />;
                } else {
                    return <ParticipantsTable user={user} participants={participants} {...bindParticipantActions} />;
                }
        }
    }

    render() {
        const {params, user} = this.props;

        return (
            <div className="container-fluid">
                <div className="page-header">
                    <Headline signOut={this._signOut} user={user} />
                    <h1>{this._getPageTitle()}</h1>
                </div>
                <div className="row">
                    <div className="col-sm-3">
                        <Sidebar params={params} user={user} />
                    </div>
                    <div className="col-sm-9">
                        {this._getContent()}
                    </div>
                </div>
            </div>
        );
    }
}

Layout.propTypes = {
    dispatch: PropTypes.func.isRequired,
    history: PropTypes.object.isRequired,
    params: PropTypes.object.isRequired,
    user: PropTypes.object.isRequired,
    participants: PropTypes.array.isRequired
};

export default Layout;



