import {PropTypes} from 'react';
import Form from '../global/Form';

import participantFormFields from './participantFormFields';

class ParticipantForm extends Form {
    componentDidMount() {
        const {params, getParticipant} = this.props;

        if (params.id !== 'new') {
            getParticipant({id: params.id});
        }
    }
    componentWillReceiveProps(nextProps) {
        super.componentWillReceiveProps(nextProps);
        const {participant, history} = this.props;

        if (!participant.id && nextProps.participant.id) {
            history.replaceState(null, `admin/participants/${nextProps.participant.id}/edit`);
        }
    }
    componentWillUnmount() {
        const {resetParticipant} = this.props;
        resetParticipant();
    }
    save() {
        const {saveParticipant} = this.props;
        saveParticipant(this.state.participant);
    }
    edit() {
        const {saveParticipant} = this.props;
        saveParticipant(this.state.participant);
    }
}

ParticipantForm.propTypes = {
    history: PropTypes.object.isRequired,
    params: PropTypes.object.isRequired,
    resetParticipant: PropTypes.func.isRequired,
    getParticipant: PropTypes.func.isRequired,
    saveParticipant: PropTypes.func.isRequired,
    participant: PropTypes.object.isRequired
};

ParticipantForm.prototype.stateNamespace = 'participant';
ParticipantForm.prototype.formFields = participantFormFields;

export default ParticipantForm;



