import {
    RESET_STATE,
    PASSWORD_SENT,
    REMIND_ERROR
} from 'actions/remindPassword';

const defaultState = {
    isSent: false,
    success: null,
    error: null
};

export default function user(state = defaultState, action) {
    switch (action.type) {
        case RESET_STATE:
            return Object.assign({}, state, defaultState);

        case PASSWORD_SENT:
            return Object.assign({}, state, {
                isSent: true,
                success: 'We have just sent you an email with temporary PIN. Enter that PIN to change your password.'
            });

        case REMIND_ERROR:
            return Object.assign({}, state, {
                error: 'Verify you email address, and try again'
            });

        default:
            return state;
    }
};



